<template>
    <div class="page">
        {{formData.title}}
        <div class="user-form">
            <div class="title">{{formData.title}}</div>
            <div class="desc">{{formData.desc}}</div>
            <div>{{formData.createTime}}</div>
            <a-divider></a-divider>
            <a-form>
                <a-form-item v-for="item in formData.formItems" :label="item.question" :colon="false">
                    <component :is="item.component" v-model="item.meta"></component>
                </a-form-item>
            </a-form>
        </div>
    </div>
</template>

<script>

    import {mapActions, mapState} from 'vuex'

    import {form as formTypes} from '../../../store/types'

    import MCascade from '@/components/formEditItems/Cascade'
    import MSelect from '@/components/formEditItems/Select'
    import MInput from '@/components/formEditItems/Input'
    import MTextarea from '@/components/formEditItems/Textarea'
    import MRate from '@/components/formEditItems/Rate'
    import MCheckbox from '@/components/formEditItems/Checkbox'
    import MDate from '@/components/formEditItems/Date'

    export default {
        name: "PC",
        components: {MCascade, MSelect, MInput, MTextarea, MRate, MCheckbox, MDate},
        props: ['formId'],
        computed: {
            ...mapState({
                formData: state => state.form.formData
            })
        },
        mounted() {
            this.loadFormData(this.formId);
        },
        methods: {
            ...mapActions({
                loadFormData: formTypes.FIND_BY_ID
            })
        }
    }
</script>

<style scoped lang="less">
    .page {
        width: 100%;
        height: 100%;
        background: #cccccc;

        .user-form {
            height: 100%;
            overflow-y: auto;
            max-width: 800px;
            margin: 0 auto;
            padding: 25px 20px;
            background: white;

            .title {
                text-align: center;
                font-weight: bold;
                font-size: 20px;
                margin: 15px 0;
                color: #c76d07;
            }

            .desc {
                font-size: 13px;
                color: #b3b3b3;
            }
        }
    }

</style>